/* tab.css */
.tab_bar{
  position: fixed; left: 0; right: 0; bottom: 0;
  width: 100%; height: 6.4%; min-height: 54px;
  border-top: 1px solid #c1c1c1;
  background-color: #f7f7f7;
  >ul{
    display: flex; flex-direction: row;
    margin-top: 10px;
    >li{
      flex: 1;
      /* border: 1px solid red; */
      text-align: center;
      transition: transform 500ms linear 0s;
      >a{
        color: #c0c1c2;
        >i{
          display: block;
          font-size: 22px;
        }
      }
      .active{
        color : #513736;
      }
    }
    li:hover{
      transform: scale(1.1);
    }
  }
}